.products-section {
    width: 1140px;
    position: relative;
    text-align: center;
}

.title {
    text-shadow: 0px 1px 1px #000000;
    color: #ffffff;
    font-family: 'Exo2.0';
    font-size: 36px;
    font-weight: 100;
    line-height: 44px;

    @media (max-width: 576px){
        font-size: 32px;
        line-height: 40px;
    }
}

.product-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    
    @media (max-width: 1200px){
        justify-content: space-around;
    }
}